@media(max-width:991px){
    .roww{
        grid-template-columns: 1fr;
        grid-gap:50px;
    }
    .roww .contentwr{
        padding-left: 0;
    }
}
@media(max-width:768px){
    .roww{
        width: 90%;
    }
    .roww .contentwr h2{
        font-size: 40px;
        font-weight: 700;
        padding-bottom: 20px;
    }
    .row .contentwr p{
        font-size: 16px;
        line-height: 25px;
        padding-bottom: 25px;
    }
}