*{
    padding: 0;
    margin: 0;
   
}
:root{
     --primary-hue: 210;
     --gray-hue: 240; 
    --color-primary: hsl(var(--primary-hue),  100%,50%);
    --color-primary-variant: hsl(var(--primary-hue), 100%,65%);
    --color-secondary: #9f8144;
    --color-gray-100: hsl(var(--gray-hue),47%, 94%);
    --color-gray-200: hsl(var(--gray-hue),26%,75%);
    --color-gray-300: hsl(var(--gray-hue),24%,64%);
    --color-gray-400: hsl(var(--gray-hue),16%,41%);
    --color-gray-500: hsl(var(--gray-hue),44%,25%);
    --color-gray-600: hsl(var(--gray-hue),48%,15%);

    --container-width-lg:80%;
    --container-width-md:90%;

    --transition: all 500ms ease;
}
body{
     font-family: 'Montserrat', sans-serif;
     color: var(--color-gray-200);
     line-height: 1.7;
     overflow-x: hidden;
     background: var(--color-gray-600); 
    
} 
.content{
    position: absolute;
    top: 50%;
    left: 7%;
    transform: translateY(-50%); 
}
 .kil{
    color: rgb(255, 42, 0);
    font-weight: bold;
}
h1{
    color: white;
    margin: 20px 0px 20px;
    font-size: 50px;
    font-weight: bold;
}
h3{
    color: white;
    font-size: 25px;
   margin-bottom: 50px;
}
p{
    color: #fcfc;
}
.work{
    color: #fcfc;
    letter-spacing: 3px;
    font-size: 20px;
}
.about img{
    height: auto;
    width: 450px;
    height: 500px;
    /* box-shadow: 0px 0px 5px #000, 0px 0px 15px #432c2c; */
     border-radius: 10px;
     margin-right: 40px;
     margin-top: -30px;
}
.main{
    width: 1730px;  
    max-width: 200%;  
    margin-top: 60px;
    margin-right: 30px;
    display: flex;
    justify-content: end;  
    padding: 0px 200px; 
}

/* {
    color: white;
    font-size: large;
    border-radius: 5px;
    
} */
.btn-login:hover{
    color: white;
    transform: scale(1.1);
    background:var(--color-secondary);
} 
.content button{
    margin-top: 30px;
}
    @media only screen and (max-width: 768px) {
    h1 {
        font-size: 40px;
    }
    h3 {
        font-size: 20px;
    }
    .about img {
        width: 100%;
        margin-right: 0;
        margin-top: 20px;
    }
    .main {
        flex-direction: column; 
        padding: 0px 10px;
    }
}

@media only screen and (max-width: 480px) {
    h1 {
        font-size: 30px;
    }
    h3 {
        font-size: 18px;
    }
} 

.programs{
    margin-top: 4rem;
}
.programs_wrapper{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 3.5rem;
    margin-top: 2rem;
}
  .extra h5{
    font-weight: bold;
    color:aliceblue;
    font-size:large;
} 

                    /* Testimonials */
.testimonial_avatar{
    width: 100%;
    text-align:center;
    margin-bottom: 1rem;
    border: 2px solid transparent;
    width: 4rem;
    height: 4rem;
    border-radius: 2rem;
    overflow: hidden;
    position: relative;
    top: -5rem;
    left: calc(50% - 2rem);
    box-shadow: 0 1.5rem 2rem rgba(26, 26, 54, 0.4);
    transition: var(--transition);

    /* margin-inline: auto; */
}
.testimonial:hover .testimonial_avatar{
    border-radius: 50%;
    /* border-color: var(--color-gray-400); */
}
.testimonials_quote{
    font-style: italic;
    margin: 1rem 0 2rem;
}
.card small.testimonials_title{
    margin-top: 0.3rem;
}
.testimonial_avatar img {
    max-width: 100%; 
    height: auto;
    border-radius: 0%;
}
.testimonials_btn-container{
    margin-right: 5rem;
    margin: 2.5rem auto 0;
    display: flex;
    justify-content: center;
    gap: 1rem;
    background: transparent;
    cursor: pointer;
}  
.testimonials_btn{
    background: transparent;
    cursor: pointer;
} 
.testimonials_btn svg{
    fill: var(--color-primary);
    font-size: 1.8rem;
    width: 3rem;
    height: 3rem;
} 

.testimonials{
    width: 50rem;
    margin-inline: auto;
    
}
.testimonials_head{
    justify-content: center;
    padding-bottom: 4rem;
    font-weight: bolder;
}
.card.testimonial{
    position: relative;
    text-align: left;
}
@media only screen and (max-width: 768px) {
    .testimonial_avatar {
        width: 3rem;
        height: 3rem;
        top: -1.5rem;
        left: 50%;
        transform: translateX(-50%);
    }

    .testimonials_quote {
        font-size: 0.9rem; /* Reduced font size for smaller screens */
        margin: 0.5rem 0 1.5rem; /* Reduced margins for smaller screens */
    }

    .testimonials_head {
        padding-bottom: 1rem; /* Reduced padding for smaller screens */
    }

    .testimonials {
        width: 95%; /* Adjusted width for smaller screens */
    }
}
@media only screen and (max-width: 480px) {
    .testimonials {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    .card.testimonial {
        flex-basis: calc(50% - 1rem); /* Adjust margin between containers */
        margin-bottom: 2rem; /* Adjust space between rows */
    }
}




/*Footer*/

footer{
    background: hsl(210deg 21.04% 30.26%);
    margin-top: 7rem;
    padding-top: 3rem;
    text-decoration: none;
    list-style-type: none;
    font-size: 0.9rem;
    color: var(--color-gray-100);
}
.footer_container{
    display: grid;
    grid-template-columns: 26rem 1fr 1fr 1fr;
    gap: 6rem;
}
.makit a{
    gap: 2rem;
}
.footer_container article{
    text-decoration: column;
    align-items: flex-start;
    gap: 0.9rem;
}
.footer_container article p{
    margin-top: 1rem;
    text-align: center;
    margin-right: 10%;
}
.footer_container article h4{
    margin-bottom: 0.7rem;
    /* font-size: 1rem; */
}
.footer_socials{
    margin-top: 1.5rem;
    display: flex;
    gap: 1rem;
    align-items: center;
    /* width: 4rem; */
}
.footer_socials a{
    background: var(--color-gray-100);
    /* background: var(--color-primary); */
    /* padding: 0.2rem 0.6rem; */
    border: 1px solid transparent;
    font-size: x-large !important;
    border-radius: 0.6rem;
    transition: var(--transition);
    font-size: 1.3rem;
   height: 3rem;
   width: 3rem;
}
.footer_socials a svg{
    color: var(--color-gray-600);
}
.footer_socials a:hover{
    /* border-color: var(--color-gray-100); */
    /* border-color: var(--color-gray-400);
    background: transparent; */
    background: var(--color-secondary);
    color: var(--color-gray-600);
}
.footer_socials a:hover span{
    background: var(--color-secondary);
    color: var(--color-gray-600);
    /* hsl(210deg 11.88% 40.85%) */
}
.footer_copyright{
    color: var(--color-gray-100);
    text-align: center;
    font-size: 1.1rem;
    /* align-items: center; */
    padding: 1.5rem 0;
    border-top: 2px solid hsl(212, 14%, 47%);;
    margin-top: 3rem;
}
.banner img{
    width: 15rem;
    display: flex;
    /* display: block; */
}
