 @import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Nunito:wght@500;700&family=Poppins:wght@500&display=swap'); 

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
   
}
 li,a,button{
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 16px;
    text-decoration: none;
 }
 .logo{
    width: 100%;
    height: 90px;

    
 }
 
header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 5%; 
    background-image: linear-gradient(to right, #141e30, #243b55);  
    box-shadow: 0px 0px 5px #000, 0px 0px 15px #432c2c;
    height: 90px;
}
.nav-links{
    list-style: none;
}
.nav-links li{
    display: inline-block;
    padding: 10px 6px;
    color: white;
    margin-top: 15px;
    margin-right: 15px;
}
.nav-links li a{
    transition: all 0.3s ease 0s;
    color: white;
    padding: 10px 10px;
    text-transform: uppercase;
    border-radius: 5px;
    transition: .5s;
    font-weight: 600;
    
}
.nav-links li a:hover{
    background-color: #131417;
   
}
.active{
    background-color: #131417;
} 
.btn-login { 
    /* padding: 5px 15px; */
    background-color: rgb(0,136,169,0.8);
    box-shadow: 0px 0px 5px #000, 0px 0px 15px #716666;
    color: white;
    text-decoration: none;
    border: 2px solid transparent;
    font-weight: bold;
    padding: 10px 20px;
    border-radius: 40px;
    transition: transform .4s; 
}
.btn-login:hover{
    transform: scale(1.1);
    background:var(--color-secondary);
}  


