.roww{
    width: 90%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap:50px;
    padding: 5rem 0;
    position: relative;
    left: 5%;
}
h2{
    text-shadow: 2px 2px rgb(25, 103, 170);
}
.roww .imagewr{
    overflow: hidden;
 box-shadow: rgba(234, 236, 235, 0.84) 0px 3px 8px;
 border-radius: 10px;
}
.roww .imagewr img{
    /* width: 100%;
    height: 100%; */
    object-fit: cover;
    transition: 0.3s;

}
.roww .imagewr:hover img{
    transform: scale(1.1);
}
.roww .contentwr{
    width: 100%;
    display: flex;
    align-items: center;
    left: 100px;
    position: relative;
}
.roww .contentwwr span.textwr{
    width: 100%;
    text-shadow: 2px 3px;
    display: flex;
    align-items: center;
    position: relative;
}
.roww .contentwr span.textwr{
    display: block;
    text-shadow: 3px 2px;
    font-size: 25px;
    display: flex;
    gap: 15px;
    margin-bottom: 20px;
}