html, body {
    height: 100%;
}
.page-container{
    min-height: 20vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.wrapper2 {
    width: 40%; height: 90%;  margin: 0 auto; padding: 20px; border-radius: 10px;
    /* background: transparent; */
    border: 2px solid rgba(255, 255, 255, .2);
    /* box-shadow: #f30f0f; */
    box-shadow: inset 0 0 10px rgba(177, 3, 241, 0.5);
}
.wrapper2 h1{
    font-size: 36px; 
    text-align: center;
}
.wrapper2 .input-box{
    height: 70%;
    text-align: center;
    align-items: center;
}
.input-box input::placeholder{
    color: aliceblue;
}

input[type="file"] {
    display: none;
  }

.custom-file-upload,
.form-control {
    background-color: aqua;
    /* width: 60%; */
    height: 40px;
    padding: 20px 45px 20px 20px;
    outline: none;
    border: 2px solid rgba(255, 255, 255, .2);
    border-radius: 40px;
    background: transparent;
}

.shadow-lg {
    box-shadow: 0 1rem 3rem rgba(255,255,255,1)!important;
}
  
/*   .custom-file-upload {
    border: 1px solid #ccc;
    display: inline-block;
    padding: 6px 12px;
    cursor: pointer;
  } */

.wrapper2 .remember-forget a:hover{
    text-decoration: underline;
}
.wrapper2 .remember-forget a{
    color: aliceblue;
}
.wrapper2 .loogg{
    width: 100%;
    height: 50px;
    border: none;
    border-radius: 40px;
    background-color: rgb(0, 136, 169, 0.8);
    outline: none;
    box-shadow: rgba(0, 0, 0, .1);
    cursor: pointer;
    font-size: 16px;
    color: #fff;
    /* color: #333; */
    font-weight: 600;
    box-shadow: inset 0 0 10px rgba(241, 3, 66, 0.5);
}
.loogg:hover{
    
    background: transparent;
    color: #fff;
}
.wrapper2 .register-link{
    font-size: 14.5px;
    text-align: center;
    margin: 20px 0 15px;
}
.register-link p a{
    color: #fff;
    text-decoration: none;
    font-weight: 600;
}
.register-link p a:hover{
    text-decoration: underline;
}


 .input-box {
    margin-bottom: 10px;
}

.remember-forget {
    margin-bottom: 10px;
    display: flex;
    justify-content: space-between;
    accent-color: white;
}
.remember-forget label input{
    accent-color: white;
    margin-right: 3px;
}

.register-link {
    margin-top: 10px;
} 

