.middle{
    position: absolute;
    top: 50%;
    left: 50%; 
    transform: translate(-50%,-50%);
    text-align: center;
    border-radius: 50px;
}
.btnn{
    background: none;
    color: aliceblue;
    border: 2px solid #ffffff;
    text-transform: uppercase;
    padding: 12px 20px;
    margin-right: 10px;
    margin: 25px;
    cursor: pointer;
    display: block;
    transition: color 0.4s linear;
    position: relative;
    border-radius: 25px;
    width: 100%;
    height: 70px;
    box-shadow: 0px 0px 5px #000, 0px 0px 15px #432c2c;
}
.btnn:hover{
    color: #fff;
}
.btnn::before{
    content:'';
    position: absolute;
    border-radius: 50px;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #4158D0;
    background-image: linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);
    z-index: -1;
    transform: scale(1.1);
    transition: transform 0.5s;
    transform-origin: 0 0;
    transition-timing-function: cubic-bezier(0.7,1,0.4,0.7);
}
.btn1::before{
    transform: scaleX(0);
    border-radius: 50px;
}
.btn1:hover::before{
    transform: scaleX(1);
    border-radius: 50px;
}
.btn2::before{
    transform: scaleY(0);
    border-radius: 50px;
}
.btn2:hover::before{
    transform: scaleY(1);
    border-radius: 50px;
}
