/* Make input borders more prominent */
input.form-control {
    border: 2px solid #ced4da; /* Default Bootstrap border color */
    box-shadow: none; /* Remove any additional shadows */
  }
  
  input.form-control:focus {
    border-color: #80bdff; /* Highlight border color when focused */
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25); /* Default Bootstrap shadow */
  }
  
  /* Optional: Style the profile button */
  .profile-button {
    background-color: #007bff;
    border: none;
  }
  
  .profile-button:hover {
    background-color: #0056b3;
  }
  