.headerr{
    /* margin-top: 5rem; */
    height: 20rem;
    overflow: hidden;
    border-bottom: 2px solid var(--color-gray-400);
    /* width: 100%; */
  }
  .header_container{
    width: 100%;
    height: 100%;
    position: relative;
    display: grid;
    place-items: center;
   
    
  }
  .header_content{
    position: relative;
    width: 44%;
    margin: auto 0;
    text-align: center;
    color: var(--color-gray-100);
  }
  .header_content h2{
    margin-bottom: 1rem;
    font-weight: bold;
  }
  .header_content p{
    color: rgba(230, 222, 222, 0.7);
  }
  .header_container-bg{
    width: 100%;
    position: absolute;
    opacity: 0.4;
  }
  .about_section-image img{
    border-radius: 50%;
    width: 40rem;
    height: 25rem;
    overflow: hidden;
    transform: skew(8deg);
    transition: var(--transition);
  }
  .about_section-image img:hover{
    transform: skew(0);
  }
  .about_section-content h1{
    margin-bottom: 2rem;
    left: -5%;
    position: relative;
  }
  .about_section-content p{
    margin-bottom: 2rem;
    left: -5%;
    position: relative;
  }
  .about_story-container{
    display: grid;
    grid-template-columns: 50% 50%;
    gap: 10%;
  }
 