*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.programs{
    padding: 10px 10px;
}



section{
    margin-top: 10rem;
    align-items: center;
}
.section_head{
    display: flex;
    align-items: center;
    gap: 1rem;
}
.section_head span{
    background: rgb(60, 79, 96);
    padding: 0.3rem 1rem;
    border-radius: 1rem;
    color: rgb(199, 162, 16);
    font-size: 2rem;
}
.card-prog{
    background-color: var(--color-gray-500);
    border: 2px solid transparent;
    text-align: center;
    padding: 3rem 2rem;
    margin-bottom: 1.5rem;
    border-radius: 0 3rem 0 3rem;
    transition: var(--transition);
    width: 100%;
}
.card-prog:hover{
    background: transparent;
    /* background-color: var(--color-gray-400); */
    filter: saturate(1);
    border: 2px solid black;
    cursor: default;
}
.card-prog span{
    width: 4rem;
    height: 4rem;
    background: var(--color-primary);
    color: var(--color-gray-100);
    font-size: 1.5rem;
    padding: 0.1rem;
    display: grid;
    place-items: center;
    margin-top: -1.5rem;
    margin-bottom: 1.5rem;
    margin-inline: auto;
    border-radius: 1rem;
    transition: var(--transition);
}
.card-prog:hover span{
    background: var(--color-secondary);
    color: var(--color-gray-600);
}
.card-prog small{
    margin-top: 1rem;
    display: block;
    font-weight: 200;
    font-size: medium;
}

    


                 